 // States\n  &:focus,\n  &.is-focused,\n  &:active,\n  &.is-active\n    outline: none\n  &[disabled],\n  fieldset[disabled] &\n    cursor: not-allowed\n\n// The controls sizes use mixins so they can be used at different breakpoints\n=control-small\n  border-radius: $control-radius-small\n  font-size: $size-small\n=control-medium\n  font-size: $size-medium\n=control-large\n  font-size: $size-large\n","// Colors\n\n$black:        hsl(0, 0%, 4%) !default\n$black-bis:    hsl(0, 0%, 7%) !default\n$black-ter:    hsl(0, 0%, 14%) !default\n\n$grey-darker:  hsl(0, 0%, 21%) !default\n$grey-dark:    hsl(0, 0%, 29%) !default\n$grey:         hsl(0, 0%, 48%) !default\n$grey-light:   hsl(0, 0%, 71%) !default\n$grey-lighter: hsl(0, 0%, 86%) !default\n$grey-lightest: hsl(0, 0%, 93%) !default\n\n$white-ter:    hsl(0, 0%, 96%) !default\n$white-bis:    hsl(0, 0%, 98%) !default\n$white:        hsl(0, 0%, 100%) !default\n\n$orange:       hsl(14,  100%, 53%) !default\n$yellow:       hsl(44,  100%, 77%) !default\n$green:        hsl(153, 53%,  53%) !default\n$turquoise:    hsl(171, 100%, 41%) !default\n$cyan:         hsl(207, 61%,  53%) !default\n$blue:         hsl(229, 53%,  53%) !default\n$purple:       hsl(271, 100%, 71%) !default\n$red:          hsl(348, 86%, 61%) !default\n\n// Typography\n\n$family-sans-serif: BlinkMacSystemFont, -apple-system, \"Segoe UI\", \"Roboto\", \"Oxygen\", \"Ubuntu\", \"Cantarell\", \"Fira Sans\", \"Droid Sans\", \"Helvetica Neue\", \"Helvetica\", \"Arial\", sans-serif !default\n$family-monospace: monospace !default\n$render-mode: optimizeLegibility !default\n\n$size-1: 3rem !default\n$size-2: 2.5rem !default\n$size-3: 2rem !default\n$size-4: 1.5rem !default\n$size-5: 1.25rem !default\n$size-6: 1rem !default\n$size-7: 0.75rem !default\n\n$weight-light: 300 !default\n$weight-normal: 400 !default\n$weight-medium: 500 !default\n$weight-semibold: 600 !default\n$weight-bold: 700 !default\n\n// Spacing\n\n$block-spacing: 1.5rem !default\n\n// Responsiveness\n\n// The container horizontal gap, which acts as the offset for breakpoints\n$gap: 32px !default\n// 960, 1152, and 1344 have been chosen because they are divisible by both 12 and 16\n$tablet: 769px !default\n// 960px container + 4rem\n$desktop: 960px + (2 * $gap) !default\n// 1152px container + 4rem\n$widescreen: 1152px + (2 * $gap) !default\n$widescreen-enabled: true !default\n// 1344px container + 4rem\n$fullhd: 1344px + (2 * $gap) !default\n$fullhd-enabled: true !default\n\n// Miscellaneous\n\n$easing: ease-out !default\n$radius-small: 2px !default\n$radius: 4px !default\n$radius-large: 6px !default\n$radius-rounded: 9999px !default\n$speed: 86ms !default\n\n// Flags\n\n$variable-columns: true !default\n$rtl: false !default\n","@charset \"utf-8\";\r\n\r\n// Import a Google Font\r\n@import url('https://fonts.googleapis.com/css?family=Nunito:400,700');\r\n\r\n// Set your brand colors\r\n$purple: #8A4D76;\r\n$pink: #FA7C91;\r\n$brown: #757763;\r\n$beige-light: #D0D1CD;\r\n$beige-lighter: #EFF0EB;\r\n\r\n// Update Bulma's global variables\r\n$family-sans-serif: \"Nunito\", sans-serif;\r\n$grey-light: $beige-light;\r\n$primary: rgb(28,40,49);\r\n$link: $pink;\r\n$widescreen-enabled: false;\r\n$fullhd-enabled: false;\r\n\r\n// Update some of Bulma's component variables\r\n\r\n// Import only what you need from Bulma\r\n@import \"../node_modules/bulma/sass/utilities/_all.sass\";\r\n@import \"../node_modules/bulma/sass/base/_all.sass\";\r\n@import \"../node_modules/bulma/sass/elements/button.sass\";\r\n@import \"../node_modules/bulma/sass/elements/container.sass\";\r\n@import \"../node_modules/bulma/sass/elements/title.sass\";\r\n@import \"../node_modules/bulma/sass/form/_all.sass\";\r\n@import \"../node_modules/bulma/sass/components/navbar.sass\";\r\n@import \"../node_modules/bulma/sass/layout/hero.sass\";\r\n@import \"../node_modules/bulma/sass/layout/section.sass\";\r\n@import \"../node_modules/bulma/sass/grid/columns.sass\";\r\n@import \"../node_modules/bulma/sass/components/card.sass\";\r\n@import \"../node_modules/bulma/sass/elements/table.sass\";\r\n@import \"../node_modules/bulma/sass/components/modal.sass\";","@import \"derived-variables\"\n\n=clearfix\n  &::after\n    clear: both\n    content: \" \"\n    display: table\n\n=center($width, $height: 0)\n  position: absolute\n  @if $height != 0\n    left: calc(50% - (#{$width} * 0.5))\n    top: calc(50% - (#{$height} * 0.5))\n  @else\n    left: calc(50% - (#{$width} * 0.5))\n    top: calc(50% - (#{$width} * 0.5))\n\n=fa($size, $dimensions)\n  display: inline-block\n  font-size: $size\n  height: $dimensions\n  line-height: $dimensions\n  text-align: center\n  vertical-align: top\n  width: $dimensions\n\n=hamburger($dimensions)\n  cursor: pointer\n  display: block\n  height: $dimensions\n  position: relative\n  width: $dimensions\n  span\n    background-color: currentColor\n    display: block\n    height: 1px\n    left: calc(50% - 8px)\n    position: absolute\n    transform-origin: center\n    transition-duration: $speed\n    transition-property: background-color, opacity, transform\n    transition-timing-function: $easing\n    width: 16px\n    &:nth-child(1)\n      top: calc(50% - 6px)\n    &:nth-child(2)\n      top: calc(50% - 1px)\n    &:nth-child(3)\n      top: calc(50% + 4px)\n  &:hover\n    background-color: bulmaRgba(black, 0.05)\n  // Modifers\n  &.is-active\n    span\n      &:nth-child(1)\n        transform: translateY(5px) rotate(45deg)\n      &:nth-child(2)\n        opacity: 0\n      &:nth-child(3)\n        transform: translateY(-5px) rotate(-45deg)\n\n=overflow-touch\n  -webkit-overflow-scrolling: touch\n\n=placeholder\n  $placeholders: ':-moz' ':-webkit-input' '-moz' '-ms-input'\n  @each $placeholder in $placeholders\n    &:#{$placeholder}-placeholder\n      @content\n\n=reset\n  -moz-appearance: none\n  -webkit-appearance: none\n  appearance: none\n  background: none\n  border: none\n  color: currentColor\n  font-family: inherit\n  font-size: 1em\n  margin: 0\n  padding: 0\n\n// Responsiveness\n\n=from($device)\n  @media screen and (min-width: $device)\n    @content\n\n=until($device)\n  @media screen and (max-width: $device - 1px)\n    @content\n\n=mobile\n  @media screen and (max-width: $tablet - 1px)\n    @content\n\n=tablet\n  @media screen and (min-width: $tablet), print\n    @content\n\n=tablet-only\n  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px)\n    @content\n\n=touch\n  @media screen and (max-width: $desktop - 1px)\n    @content\n\n=desktop\n  @media screen and (min-width: $desktop)\n    @content\n\n=desktop-only\n  @if $widescreen-enabled\n    @media screen and (min-width: $desktop) and (max-width: $widescreen - 1px)\n      @content\n\n=until-widescreen\n  @if $widescreen-enabled\n    @media screen and (max-width: $widescreen - 1px)\n      @content\n\n=widescreen\n  @if $widescreen-enabled\n    @media screen and (min-width: $widescreen)\n      @content\n\n=widescreen-only\n  @if $widescreen-enabled and $fullhd-enabled\n    @media screen and (min-width: $widescreen) and (max-width: $fullhd - 1px)\n      @content\n\n=until-fullhd\n  @if $fullhd-enabled\n    @media screen and (max-width: $fullhd - 1px)\n      @content\n\n=fullhd\n  @if $fullhd-enabled\n    @media screen and (min-width: $fullhd)\n      @content\n\n=ltr\n  @if not $rtl\n    @content\n\n=rtl\n  @if $rtl\n    @content\n\n=ltr-property($property, $spacing, $right: true)\n  $normal: if($right, \"right\", \"left\")\n  $opposite: if($right, \"left\", \"right\")\n  @if $rtl\n    #{$property}-#{$opposite}: $spacing\n  @else\n    #{$property}-#{$normal}: $spacing\n\n=ltr-position($spacing, $right: true)\n  $normal: if($right, \"right\", \"left\")\n  $opposite: if($right, \"left\", \"right\")\n  @if $rtl\n    #{$opposite}: $spacing\n  @else\n    #{$normal}: $spacing\n\n// Placeholders\n\n=unselectable\n  -webkit-touch-callout: none\n  -webkit-user-select: none\n  -moz-user-select: none\n  -ms-user-select: none\n  user-select: none\n\n=arrow($color: transparent)\n  border: 3px solid $color\n  border-radius: 2px\n  border-right: 0\n  border-top: 0\n  content: \" \"\n  display: block\n  height: 0.625em\n  margin-top: -0.4375em\n  pointer-events: none\n  position: absolute\n  top: 50%\n  transform: rotate(-45deg)\n  transform-origin: center\n  width: 0.625em\n\n=block($spacing: $block-spacing)\n  &:not(:last-child)\n    margin-bottom: $spacing\n\n=delete\n  +unselectable\n  -moz-appearance: none\n  -webkit-appearance: none\n  background-color: bulmaRgba($scheme-invert, 0.2)\n  border: none\n  border-radius: $radius-rounded\n  cursor: pointer\n  pointer-events: auto\n  display: inline-block\n  flex-grow: 0\n  flex-shrink: 0\n  font-size: 0\n  height: 20px\n  max-height: 20px\n  max-width: 20px\n  min-height: 20px\n  min-width: 20px\n  outline: none\n  position: relative\n  vertical-align: top\n  width: 20px\n  &::before,\n  &::after\n    background-color: $scheme-main\n    content: \"\"\n    display: block\n    left: 50%\n    position: absolute\n    top: 50%\n    transform: translateX(-50%) translateY(-50%) rotate(45deg)\n    transform-origin: center center\n  &::before\n    height: 2px\n    width: 50%\n  &::after\n    height: 50%\n    width: 2px\n  &:hover,\n  &:focus\n    background-color: bulmaRgba($scheme-invert, 0.3)\n  &:active\n    background-color: bulmaRgba($scheme-invert, 0.4)\n  // Sizes\n  &.is-small\n    height: 16px\n    max-height: 16px\n    max-width: 16px\n    min-height: 16px\n    min-width: 16px\n    width: 16px\n  &.is-medium\n    height: 24px\n    max-height: 24px\n    max-width: 24px\n    min-height: 24px\n    min-width: 24px\n    width: 24px\n  &.is-large\n    height: 32px\n    max-height: 32px\n    max-width: 32px\n    min-height: 32px\n    min-width: 32px\n    width: 32px\n\n=loader\n  animation: spinAround 500ms infinite linear\n  border: 2px solid $grey-lighter\n  border-radius: $radius-rounded\n  border-right-color: transparent\n  border-top-color: transparent\n  content: \"\"\n  