normal\n  font-size: $size-normal\n=button-medium\n  font-size: $size-medium\n=button-large\n  font-size: $size-large\n\n.button\n  @extend %control\n  @extend %unselectable\n  background-color: $button-background-color\n  border-color: $button-border-color\n  border-width: $button-border-width\n  color: $button-color\n  cursor: pointer\n  @if $button-family\n    font-family: $button-family\n  justify-content: center\n  padding-bottom: $button-padding-vertical\n  padding-left: $button-padding-horizontal\n  padding-right: $button-padding-horizontal\n  padding-top: $button-padding-vertical\n  text-align: center\n  white-space: nowrap\n  strong\n    color: inherit\n  .icon\n    &,\n    &.is-small,\n    &.is-medium,\n    &.is-large\n      height: 1.5em\n      width: 1.5em\n    &:first-child:not(:last-child)\n      +ltr-property(\"margin\", calc(#{-0.5 * $button-padding-horizontal} - #{$button-border-width}), false)\n      +ltr-property(\"margin\", $button-padding-horizontal * 0.25)\n    &:last-child:not(:first-child)\n      +ltr-property(\"margin\", $button-padding-horizontal * 0.25, false)\n      +ltr-property(\"margin\", calc(#{-0.5 * $button-padding-horizontal} - #{$button-border-width}))\n    &:first-child:last-child\n      margin-left: calc(#{-0.5 * $button-padding-horizontal} - #{$button-border-width})\n      margin-right: calc(#{-0.5 * $button-padding-horizontal} - #{$button-border-width})\n  // States\n  &:hover,\n  &.is-hovered\n    border-color: $button-hover-border-color\n    color: $button-hover-color\n  &:focus,\n  &.is-focused\n    border-color: $button-focus-border-color\n    color: $button-focus-color\n    &:not(:active)\n      box-shadow: $button-focus-box-shadow-size $button-focus-box-shadow-color\n  &:active,\n  &.is-active\n    border-color: $button-active-border-color\n    color: $button-active-color\n  // Colors\n  &.is-text\n    background-color: transparent\n    border-color: transparent\n    color: $button-text-color\n    text-decoration: $button-text-decoration\n    &:hover,\n    &.is-hovered,\n    &:focus,\n    &.is-focused\n      background-color: $button-text-hover-background-color\n      color: $button-text-hover-color\n    &:active,\n    &.is-active\n      background-color: bulmaDarken($button-text-hover-background-color, 5%)\n      color: $button-text-hover-color\n    &[disabled],\n    fieldset[disabled] &\n      background-color: transparent\n      border-color: transparent\n      box-shadow: none\n  &.is-ghost\n    background: $button-ghost-background\n    border-color: $button-ghost-border-color\n    color: $button-ghost-color\n    text-decoration: $button-ghost-decoration\n    &:hover,\n    &.is-hovered\n      color: $button-ghost-hover-color\n      text-decoration: $button-ghost-hover-decoration\n  @each $name, $pair in $button-colors\n    $color: nth($pair, 1)\n    $color-invert: nth($pair, 2)\n    &.is-#{$name}\n      background-color: $color\n      border-color: transparent\n      color: $color-invert\n      &:hover,\n      &.is-hovered\n        background-color: bulmaDarken($color, 2.5%)\n        border-color: transparent\n        color: $color-invert\n      &:focus,\n      &.is-focused\n        border-color: transparent\n        color: $color-invert\n        &:not(:active)\n          box-shadow: $button-focus-box-shadow-size bulmaRgba($color, 0.25)\n      &:active,\n      &.is-active\n        background-color: bulmaDarken($color, 5%)\n        border-color: transparent\n        color: $color-invert\n      &[disabled],\n      fieldset[disabled] &\n        background-color: $color\n        border-color: transparent\n        box-shadow: none\n      &.is-inverted\n        background-color: $color-invert\n        color: $color\n        &:hover,\n        &.is-hovered\n          background-color: bulmaDarken($color-invert, 5%)\n        &[disabled],\n        fieldset[disabled] &\n          background-color: $color-invert\n          border-color: transparent\n          box-shadow: none\n          co